  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    -webkit-filter: blur(10px);
            filter: blur(10px); }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes zoomIn {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
